import React, { Component } from 'react'
import { setupScrollReveal } from '../../assets/js/main.js';

import NewsletterForm from '../forms/NewsletterForm';

class Hero extends Component {
  componentDidMount() {
    setupScrollReveal();
  }

  render() {
    const { title, content } = this.props;
    const Illustration = this.props.illustration;

    return (
      <section className="hero">
        <div className="container">
          <div className="hero-inner">
            <div className="hero-copy">
              <div className="container-sm">
                <h1 className="hero-title h2-mobile mt-0 is-revealing">Manyfist- education guidance system</h1>
                <NewsletterForm className="hero-form" submit="be part of the legion" />

                <p className="hero-paragraph is-revealing"> manyfist is a movement to make india a true global village and take education,mentorship and employment to rural and semi-urban parts of the country</p>
              </div>
              <div className="container-sm">
                <h1 className="hero-title h2-mobile mt-0 is-revealing">what?</h1>
                <p className="hero-paragraph is-revealing">
                  manyfist is movement to help students from rural areas and towns get quality education,career guidance and employement oppurtunities 
                </p>
              </div>
              
              <div className="container-sm">
                <h1 className="hero-title h2-mobile mt-0 is-revealing">why?</h1>
                <p className="hero-paragraph is-revealing">
                We believe every student should be able to get free and quality education <br/>
                every skill which has ever been thought is available for free on the internet and we want remove the age old system of college education and guide  unpreviledged students get free and quality education through a proper education guidance system and mentorship
                <br/>
                <br/>
                <br/>
                <q>
                  everything which could be learned through the internet should be learned through the internet 
                </q>
                   
                   <br/> 

                </p>
              </div>

              <div className="container-sm">
                <h1 className="hero-title h2-mobile mt-0 is-revealing">But how?</h1>
                <p className="hero-paragraph is-revealing">
                  more than 60% of the youth in our coutry is from rural and smaller parts of country and we want to make sure that every single one of them has access to world class education 
                  <br/>
                  <span>this is a long term commitment and we want your help</span>
                </p>
             
                <h3 className="hero-title h3-mobile mt-0 is-revealing">But how?</h3>
                 
                <h2 className="hero-title h2-mobile mt-0 is-revealing">Professionals</h2>
                <p className="hero-paragraph is-revealing">
                if you are someone who has good expiernece in what you do, you can help us by :
                <ul>
                  <li>identifying learning resources based on cultural and language requirements of students</li>
                  <li>help create educational resources </li>
                  <li>be a mentor and guide students </li>
                  <li>help us find intership and job oppurtunities for the students</li>
                </ul>
                   <br/>
                 </p>

              <NewsletterForm className="hero-form" submit="be part of the legion" />
           
              <br/>
              <br/>
              <h2 className="hero-title h2-mobile mt-0 is-revealing">students</h2>
              <p>
                if you are someone who has never faced difficulty getting education and are skillfull in whatever you do 
                <br/>
                you can help us :
                <ul>
                  <li>create learning challenges which will improve students skillset</li>
                  <li>be a student mentor and guide other less previledged students learn </li>
                  <li>be part of the community and help and interact with others </li>
                 </ul>
              </p>

              </div>
              <NewsletterForm className="hero-form" submit="be part of the legion" />
            </div>

            <div className="hero-illustration">
              <Illustration />
            </div>
          </div>
        </div>

       
       
      </section>
    )
  }
}

export default Hero;



